// Panel Class Prefix.
$plugin-prefix: "coca_bais" !default;

##{$plugin-prefix}__edit_panel_root {
	position: relative;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	width: 100%;
	min-height: 40px;
	margin-top: 20px;
	background-color: #fff;
	border: 1px solid #c3c4c7;
	box-shadow: 0 1px 1px rgb(0 0 0 / 4%);
	transition: 300ms ease-in-out;

	&::before {
		position: absolute;
		top: 50%;
		left: 50%;
		display: block;
		width: 15px;
		height: 15px;
		margin-top: -11px;
		content: "";
		border-color: rgb(234 84 0 / 100%) transparent rgb(234 84 0 / 100%) transparent;
		border-style: solid;
		border-width: 2px;
		border-radius: 50%;
		transform: translate(-50%, -50%);
		animation: coca-dual-ring-anime 1000ms linear infinite;
	}

	.panel-wrapper {
		z-index: 10;
		display: flex;
		flex-direction: column;
		width: 100%;

		.coca-bais-panel {

			.components-tab-panel__tabs {
				border-bottom: 2px solid #c3c4c7;
			}

			.components-tab-panel__tabs-item {
				transition: 300ms ease-in-out;

				&:after {
					bottom: -1.6px;
					height: calc(var(--wp-admin-border-width-focus) * 1);
					background: #c3c4c7;
					outline: 2px solid transparent;
					outline-offset: -1px;
				}

				&.is-active:after {
					background: var(--wp-components-color-accent, var(--wp-admin-theme-color, #007cba));
				}
			}

			.components-panel {
				border: none;
			}

			.components-panel__body {
				padding: 0;

				// border-color: rgba(234, 84, 0, 1);
				transition: 300ms ease-in-out;

				.components-panel__row {
					flex-direction: column;
					gap: 0;
					align-items: flex-start;
					justify-content: flex-start;
					padding-right: 20px;
					padding-left: 20px;
					transition: 300ms ease-in-out;

					.field-control {
						// align-items: center;
						justify-content: space-between;
						width: 100%;

						&:not(.field-gap) {
							padding-top: 20px;
							padding-bottom: 20px;
						}

						&:not(.field-gap, :last-child:not(:first-child)) {
							border-bottom: 1px solid #dddd;
						}

						.field-control__label {
							max-width: 200px;
							font-size: 12px;
							text-transform: uppercase;
						}

						&,
						.fields-wrapper {
							display: flex;
							flex-direction: row;
						}

						.fields-wrapper {
							display: flex;
							flex-direction: row;
							gap: 20px;
							justify-content: flex-start;

							.components-base-control,
							.components-base-control__field {
								margin-bottom: 0;
							}

							.field {
								transition: 300ms ease-in-out;
							}

							select {
								line-height: 1.5;
							}

							fieldset,
							.components-range-control {
								width: 100%;
							}

							.components-popover__content {
								position: relative;
								min-width: max-content;
								max-height: none;
							}
						}

						> * {
							width: 100%;
						}
					}
				}

				&.is-opened .components-panel__body-title {
					background-color: rgb(234 84 0 / 100%);
				}

				.components-panel__body-title {
					background-color: #2b2a2a;
					outline: none !important;

					button,
					svg {
						color: #fff;
					}

					.components-button {
						outline: none !important;
						box-shadow: none !important;
					}

					&,
					button,
					svg {
						transition: 300ms ease-in-out;
					}

					&:hover {
						background-color: rgb(234 84 0 / 100%);

						button,
						svg {
							color: #fff;
						}
					}
				}
			}
		}

	}
}

.post-type-#{$plugin-prefix} #wpwrap {
	overflow: hidden;

	.column-shortcode {
		width: 14%;
	}

	.column-before_image,
	.column-middle_image,
	.column-after_image,
	.column-indicator_image {

		img {
			width: 80px;
			height: 80px;
		}
	}

	// stylelint-disable-next-line
	#coca_bais_copy_html {
		position: absolute;
		top: 40px;
		right: 145px;
		display: block;
		padding: 10px 20px;
		user-select: none;
		background: #ddd;
		border-radius: 5px;
		box-shadow: 0 40px 70px 0 rgb(0 0 0 / 5%);
		opacity: 0;
		transition: 300ms ease-in-out;
		transform: translateX(200%);
	}
}

@keyframes coca-dual-ring-anime {

	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}
